.modal {
	position: fixed;
	z-index: 10000;
	overflow-x: hidden;
	top: 0;
	left: 0;
	display: none;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	transition: background-color 0.2s;
	-webkit-overflow-scrolling: touch;

	&--show { // .modal--shown
		display: flex;
	}

	&--backdrop { // .modal--backdrop
		background-color: rgba(#D9AB90, .2);
	}

	&__body { // .modal__body
		position: relative;
		z-index: 1;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: calc(100% - 32px);
		min-height: 360px;
		margin: auto;
		background-color: #000;
		border-radius: 32px;
		transition: transform 0.3s ease-in-out, opacity 0.7s ease;
		opacity: 0;
		transform: perspective(600px) translateY(350px) rotateX(-45deg) scale(0.7);
		transform-origin: 50% 0;
		overflow: hidden;

		.modal--shown & {
			opacity: 1;
			transform: perspective(600px) rotateX(0) scale(1);
		}

		.modal--login & {
			max-width: 1000px;
		}
	}

	&__close {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 30px;
		height: 30px;
		padding: 0;
		border-width: 0;
		background-color: $color-transparent;
		cursor: pointer;
		color: #464646;
		transition: color $transition-default;

		&:hover {
			color: $color-orange;
		}

		&:focus {
			outline-width: 0;
		}
	}

	&__close-icon {
		width: 24px;
		height: 24px;
	}

	&__content {
		&--image-left {
			display: grid;
			grid-template: auto / repeat(2, 1fr);
		}
	}

	&__img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__content-box {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 48px;
	}

	&__title-box {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__title {
		font-family: $font-secondary;
		font-size: 32px;
		line-height: 38px;
		text-transform: uppercase;
	}

	&__checkbox {
		.modal--login & {
			margin-top: 12px;
		}
	}
}
