.form-group {
	&__label {
		display: block;
		margin-bottom: 12px;
		font-size: 18px;
		line-height: 21px;
		color: rgba($color-white, .5);

		&--center {
			text-align: center;
		}
	}

	&__input {
		display: block;
		width: 100%;
		padding: 16px 24px;
		border: 1px solid rgba($color-white, .5);
		border-radius: 50px;
		background-color: $color-transparent;
		font-size: 18px;
		line-height: 21px;
		color: $color-white;
		transition: border-color $transition-default;

		&:hover {
			border: 1px solid rgba($color-white, .8);
		}

		&:focus {
			border: 1px solid $color-white;
		}

		.form-group--error & {
			border-color: rgba(#BA3A42, .5);
		}
	}

	&__error-text {
		display: none;
		margin-top: 2px;
		font-size: 12px;
		line-height: 14px;
		color: #BA3A42;

		.form-group--error & {
			display: block;
		}
	}
}
