/* cuprum-regular - cyrillic_latin */
@font-face {
	font-display: swap;
	font-family: 'Cuprum';
	font-style: normal;
	font-weight: 400;
	src: url('../fonts/cuprum/cuprum-v25-cyrillic_latin-regular.woff2') format('woff2');
}
